<template>
  <section class="py-4">
    <div class="mb-3 d-flex justify-content-between">
      <ul class="list-unstyled">
        <li class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle"
            href="#"
            role="button"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            篩選
          </a>
          <ul class="dropdown-menu">
            <li><a class="dropdown-item" href="#">Action</a></li>
            <li><a class="dropdown-item" href="#">Another action</a></li>
            <li><hr class="dropdown-divider" /></li>
            <li><a class="dropdown-item" href="#">Something else here</a></li>
          </ul>
        </li>
      </ul>
      <button class="btn btn-dark" @click="this.$emitter.emit('addEdit', { status: true })">
        新增員工
      </button>
    </div>
    <table class="table table-striped table-hover">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">姓名</th>
          <th scope="col">電話</th>
          <th scope="col">身份證</th>
          <th scope="col">年資</th>
          <th scope="col">職別</th>
          <th scope="col">工作記錄</th>
          <th scope="col">修改</th>
        </tr>
      </thead>
      <tbody>
        <template v-for="(item, index) in workerList" :key="index">
          <tr>
            <th scope="row">{{ index + 1 }}</th>
            <td>{{ item.name }}</td>
            <td>{{ item.phone }}</td>
            <td>
              <button
                type="button"
                @click="this.$refs.idModal.showModal(item)"
                class="btn btn-outline-dark"
              >
                {{ item.idNumber }}
              </button>
            </td>
            <td>{{ item.seniority }}</td>
            <td>{{ item.position }}</td>
            <td>
              <button class="btn btn-dark" @click="this.$emitter.emit('workerList' ,item._id)">
                <i class="bi bi-journal-text"></i>
              </button>
            </td>
            <td>
              <button
                class="btn btn-success"
                @click="this.$emitter.emit('addEdit', { status: false, data: item })"
              >
                <i class="bi bi-pencil"></i>
              </button>
            </td>
          </tr>
        </template>
      </tbody>
    </table>
  </section>
  <IdCardModal ref="idModal" />
  <WorkRecordModal />
  <AddEditModal />
</template>

<script>
import { apiGetWorkers } from '@/assets/javascript/api';
import IdCardModal from '@/components/workerModal/IdCardModal.vue';
import WorkRecordModal from '@/components/workerModal/WorkRecordModal.vue';
import AddEditModal from '@/components/workerModal/AddEditModal.vue';
import { getToken } from '@/assets/javascript/methods';

export default {
  data() {
    return {
      workerList: [],
    };
  },
  components: {
    IdCardModal,
    WorkRecordModal,
    AddEditModal,
  },
  methods: {
    checkSignIn() {
      // 取得 cookie 中的 token
      const token = getToken();

      // 若有 token 則登入，反之轉為登入頁面
      if (token) {
        this.$http.defaults.headers.common.Authorization = `Bearer ${token}`;
      } else {
        this.$router.push('/login');
      }
    },
    getWorkers() {
      this.$emitter.emit('loadingStatus', true);
      apiGetWorkers()
        .then((res) => {
          this.workerList = res.data.data;
          this.$emitter.emit('loadingStatus', false);
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('loadingStatus', false);
        });
    },
  },
  mounted() {
    this.getWorkers();
    this.$emitter.on('getWorkerLsit', () => {
      this.getWorkers();
    });
  },
  created() {
    this.checkSignIn();
  },
};
</script>
