<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="AddEdit"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">
            <span v-if="status">新增員工</span><span v-else>修改員工</span>
          </h1>
          <button
            type="button"
            class="btn-close"
            @click="hideModal"
            aria-label="Close"
          ></button>
        </div>
        <VForm
          ref="formRefSignin"
          id="profileForm"
          v-slot="{ errors, meta }"
          @submit="addEditWorker"
        >
          <div class="modal-body">
            <div class="form-floating mb-3">
              <VField
                name="姓名"
                type="text"
                rules="required"
                class="form-control"
                v-model="tempData.name"
              />
              <label for="floatingInput"
                >員工姓名
                <ErrorMessage name="姓名" class="text-danger ms-3" />
              </label>
            </div>
            <div class="form-floating mb-3">
              <VField
                id="phone"
                name="phone"
                type="text"
                class="form-control"
                :class="{ 'is-invalid': errors['電話'] }"
                :rules="isPhone"
                v-model="tempData.phone"
              />
              <label for="phone"
                >phone<error-message
                  name="phone"
                  class="text-danger ms-3"
                ></error-message
              ></label>
            </div>
            <div class="form-floating mb-3">
              <VField
                name="身分證字號或居留證"
                class="form-control"
                type="text"
                rules="required|min:10"
                v-model="tempData.idNumber"
              />
              <label for="floatingInput"
                >身分證字號或居留證
                <ErrorMessage
                  name="身分證字號或居留證"
                  class="ms-3 text-danger"
                />
              </label>
            </div>
            <div class="input-group mb-3">
              <input
                class="form-control"
                type="file"
                accept="image/*"
                id="imageFront"
                name="image"
                @change="uploadImage"
              />
              <label for="imageFront" class="input-group-text"
                >上傳身分證或居留證正面</label
              >
            </div>
            <div class="input-group mb-3">
              <input
                type="file"
                class="form-control"
                accept="image/*"
                id="imageBack"
                name="image"
              />
              <label for="imageBack" class="input-group-text"
                >上傳身分證或居留證反面</label
              >
            </div>
            <div class="input-group mb-3">
              <label class="input-group-text" for="inputGroupSelect01"
                >員工職別</label
              >
              <select
                class="form-select"
                id="inputGroupSelect01"
                v-model="tempData.position"
              >
                <option value="工人" selected>工人</option>
                <option value="監工">監工</option>
                <option value="工地主任">工地主任</option>
                <option value="工頭">工頭</option>
              </select>
            </div>
            <div class="input-group mb-3">
              <label for="date" class="input-group-text">入職日期</label>
              <input
                id="date"
                name="date"
                type="date"
                placeholder="Enter email"
                class="form-control"
                v-model="tempData.seniority"
              />
            </div>
            <div class="form-floating mb-3">
              <VField
                name="時薪"
                type="text"
                v-model="tempData.hourlyRate"
                rules="numeric"
                class="form-control"
              />
              <label for="floatingInput"
                >時薪
                <ErrorMessage name="時薪" class="text-danger ms-3" />
              </label>
            </div>
          </div>
          <div class="modal-footer justify-content-between">
            <button
              type="button"
              class="btn btn-outline-danger"
              v-if="!status"
              @click="deleteProfile"
            >
              刪除員工資料
            </button>
            <div></div>
            <button
              type="submit"
              class="btn btn-primary"
              :disabled="!meta.valid"
            >
              Save changes
            </button>
          </div>
        </VForm>
      </div>
    </div>
  </div>
</template>

<script>
import { Modal } from 'bootstrap';
import {
  apiPostWorker,
  apiPatchWorker,
  apiAdminDeleteWorker,
} from '@/assets/javascript/api';

export default {
  data() {
    return {
      modal: '',
      status: false,
      tempData: {
        name: '',
        phone: '',
        idNumber: '',
        imageFront: '',
        imageBack: '',
        seniority: '',
        hourlyRate: 0,
        position: '工人',
        _id: '',
      },
    };
  },
  methods: {
    isPhone(value) {
      const phoneNumber = /^(09)[0-9]{8}$/;
      return phoneNumber.test(value) ? true : '需要正確的電話號碼';
    },
    addEditWorker() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-underscore-dangle
      if (this.tempData._id) {
        apiPatchWorker(this.tempData)
          .then(() => {
            // alert成功訊息
            this.$emitter.emit('messageModal', {
              status: true,
              message: '修改成功',
            });
            // 重新取得資料
            this.$emitter.emit('getWorkerLsit');
            // 結束loading效果
            this.$emitter.emit('loadingStatus', false);
            // 關閉modal
            this.hideModal();
          })
          .catch((err) => {
            console.log(err);
            this.$emitter.emit('loadingStatus', false);
          });
      } else {
        apiPostWorker(this.tempData)
          .then((res) => {
            console.log(res);
            this.$emitter.emit('messageModal', {
              status: true,
              message: '新增成功',
            });
            this.$emitter.emit('getWorkerLsit');
            this.$emitter.emit('loadingStatus', false);
            this.hideModal();
          })
          .catch((err) => {
            console.log(err);
            this.$emitter.emit('loadingStatus', false);
          });
      }
    },
    deleteProfile() {
      this.$emitter.emit('loadingStatus', true);
      // eslint-disable-next-line no-alert, no-underscore-dangle
      const id = prompt(`輸入該用戶ＩＤ：${this.tempData._id}`, '');
      // eslint-disable-next-line no-underscore-dangle
      if (id !== this.tempData._id) {
        this.$emitter.emit('loadingStatus', false);
        return;
      }
      // eslint-disable-next-line no-underscore-dangle
      apiAdminDeleteWorker(this.tempData._id)
        .then((res) => {
          console.log(res);
          this.$emitter.emit('messageModal', {
            status: true,
            message: '刪除成功',
          });
          this.$emitter.emit('loadingStatus', false);
          this.$emitter.emit('getWorkerLsit');
          this.hideModal();
        })
        .catch((err) => {
          console.log(err);
          this.$emitter.emit('messageModal', {
            status: false,
            message: '刪除失敗',
          });
          this.$emitter.emit('loadingStatus', false);
        });
    },
    // async uploadImage() {
    //   const formData = new FormData(document.getElementById('profileForm'));
    //   await this.upload(formData, 'photo');
    //   this.tempProfile.photo = this.tempImage.url;
    //   this.imageMsg = this.tempImage.msg;
    // },
    showModal() {
      this.modal.show();
    },
    hideModal() {
      this.tempData = {};
      this.modal.hide();
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.AddEdit);
    this.$emitter.on('addEdit', (e) => {
      this.status = e.status;
      if (!e.status) {
        this.tempData = JSON.parse(JSON.stringify(e.data));
      }
      this.showModal();
    });
  },
};
</script>
